:root {
    --backgroundColor: #4ABDAC;
    --appBar: #F7B733;
    --contrast: #e2eca9;
}
#mainContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: var(--backgroundColor);
}
#projectContainer{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    background-color: rgb(48, 156, 142);
}
#mainAbout{
    width: 1000px;
    height: 800px;
    background-color: rgb(48, 156, 142);
}
#aboutContainer{
    padding-top: 20px;
    background-color: rgb(48, 156, 142);
    border: none;
}
#aboutMeContent{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
#aboutMeContent2{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
#aboutMeContent3{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
#aboutMeContent div{
    width: 400px;
    text-align: left;
}
#aboutMeContent2 div{
    width: 400px;
    text-align: left;
}
#aboutMeContent2 h2{
    text-align: left;
}
#aboutMeContent3 div{
    width: 400px;
    text-align: left;
}
#aboutMeContent3 h2{
    text-align: left;
}
#profile-pic {
    display: block;
    margin: 0 auto;
    height: 200px;
    width: 200px;
    object-fit: cover;
    border: 2px solid var(--borderColor);
}
#skills{
    margin-top: 30px;
    display: flex;
    justify-content: space-evenly;
    background-color: var(--contrast);
}
#views{
    background-color: var(--backgroundColor);
}
#links{
    display: flex;
    justify-content: center;
    align-items: center;
}
button{
    justify-content: center;
}
.thumbnail{
    height: 100px;
    width: max-content;
}
#appBar{
    background-color: var(--appBar);
}
#vic{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--contrast);
}



